<template>
  <div class="pageing">
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[5, 10, 20, 30, 50]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
      :url="url"
    >
    </el-pagination>
  </div>
</template>

<script>
import { getTableData } from "@/utils/table";
export default {
  name: "PagIng",
  props: {
    total: Number,
    url: String,
  },
  data() {
    return {
      page: 1, // 当前页数
      size: 10, // 每页显示的条数
    };
  },
  created() {
    getTableData(this.$parent, this.url, { page: this.page, size: this.size }, [
      "completed",
    ]);
  },
  methods: {
    // 分页实现
    handleSizeChange(val) {
      this.size = val; // 修改每页显示的条数
      this.page = 1; // 重置当前页数getTableData(this.$parent, url, { page: 1, size: 5 }, ['completed'])
      getTableData(this.$parent, this.url, { page: this.page, size: val }, [
        "completed",
      ]);
    },
    handleCurrentChange(val) {
      this.page = val; // 修改当前页数
      getTableData(this.$parent, this.url, { page: val, size: this.size }, [
        "completed",
      ]);
    },
  },
};
</script>

<style>
.el-pagination {
  margin-top: 20px;
  text-align: left;
}
</style>
